<template lang="pug">
  .wrap
    h2.title 

    form.form(@submit.prevent="enterUser")
      .form__row
        label.form__label(for="email")  email
        input.form__input#email(
          type="email"
          placeholder=" Email"
          required
          v-model="user.email"
        )
      
      .form__row
        label.form__label(for="pass")  
        input.form__input#pass(
          type="password"
          placeholder=" "
          required
          v-model="user.pass"
        )

      .form__allert(v-if="error")
        <strong>!</strong> .

      .form__btn
        button.btn(type="submit") 
      
</template>

<script>
export default {
  data() {
    return {
      user: {
        email: '',
        pass: ''
      },
      error: false
    }
  },
  methods: {
    enterUser() {
      firebase.auth().signInWithEmailAndPassword(this.user.email, this.user.pass)
        .then( response => {
          const settings = {
            email: response.user.email,
            uid: response.user.uid,
            mainPage: true,
            complete: true
          }
          this.$emit('addUser', settings)
        })

    }
  }
}
</script>

<style lang="scss" src="styles/sign.scss" scoped>
  

</style>


